:root {
  --header_font_size: 38vh;
}

.header_background {
  background-color: #b84545;
  height: 100%;
  display: flex;
}

.header_center {
  padding: 0;
  width: 90%;
  height: 90%;

  margin: auto;
}

.header_font {
  padding: 0;
  margin: 0;
  color: #ffffff;
  font-size: var(--header_font_size);
  text-align: center;
  font-weight: 800;
  letter-spacing: 12px;
  line-height: 1.1;
}

@media only screen and (max-width: 600px) {
  :root {
    --header_font_size: calc(var(--vh, 1vh) * 8);
  }

  .header_center {
    width: 90%;
    height: 50%;
  }
}

@media only screen and (max-width: 1200px) and (min-width: 600px) {
  :root {
    --header_font_size: 12vh;
  }

  .header_center {
    width: 90%;
    height: 50%;
  }
}
