:root {
  /* --development_font_size: 100px;
    --development_sub_font_size: 25px; */
  --enthusiastic_font_size: 30vh;
  --enthusiastic_sub_font_size: 5vh;
}

.enthusiastic_background {
  background-color: #e0fee1;
  height: 100%;
  display: flex;
}

.enthusiastic_center {
  width: 80%;

  margin: auto;
}

.enthusiastic_title {
  width: 100%;
  height: 40%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.enthusiastic_title p {
  margin: 0;
  color: #373737;
  font-size: var(--enthusiastic_font_size);
  font-weight: 700;
}

.enthusiastic_description {
  width: 100%;
  height: 10%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 8vh;
}

.enthusiastic_description p {
  margin: 0;
  color: #373737;
  font-size: var(--enthusiastic_sub_font_size);
  font-weight: 400;
}

@media only screen and (max-width: 600px) {
  :root {
    --enthusiastic_font_size: calc(var(--vh, 1vh) * 7);
    --enthusiastic_sub_font_size: calc(var(--vh, 1vh) * 3);
  }
}

@media only screen and (max-width: 1200px) and (min-width: 600px) {
  :root {
    --enthusiastic_font_size: calc(var(--vh, 1vh) * 10);
    --enthusiastic_sub_font_size: calc(var(--vh, 1vh) * 4);
  }
}
