:root {
  /* --address_font_size: 180px;
  --address_sub_font_size: 30px;
  --location_font_size: 36px; */
  --address_font_size: 24vh;
  --address_sub_font_size: 4vh;
  --location_font_size: 5vh;
}

.address_background {
  width: 100%;
  height: 100%;
}

.grow_section {
  width: 100%;
  height: 50%;
  background-color: #4745b8;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #ffffff;
}

.center {
  width: 80%;
  height: 80%;
}

.grow_title {
  margin: 0;
}

.grow_title p {
  margin: 0;
  text-align: center;
  font-size: var(--description_font_size);
  font-weight: 700;
  line-height: 1;
}

.grow_sub_title {
  margin: 0;
  margin-top: 40px;
  text-align: center;
  font-size: var(--description_sub_font_size);
  font-weight: 500;
}

.location_section {
  width: 100%;
  height: 50%;
  background-color: #45b88f;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #ffffff;
  position: relative;
}

.location_center {
  width: 70%;
  height: 80%;
}

.location_address {
  height: 40%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.location_address img {
  width: 50px;
  height: 50px;
}

.location_address p {
  margin: 0;
  font-size: var(--location_font_size);
  margin-left: 40px;
}

.location_mail {
  height: 40%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.location_mail img {
  width: 50px;
  height: 50px;
}

.location_mail p {
  margin: 0;
  font-size: var(--location_font_size);
  margin-left: 40px;
}

.signature {
  position: absolute;
  bottom: 30px;
  left: 50%;
  font-size: 20px;
  font-weight: 600;
  transform: translate(-50%);
}

@media only screen and (max-width: 400px) {
  :root {
    --address_font_size: calc(var(--vh, 1vh) * 7);
    --address_sub_font_size: calc(var(--vh, 1vh) * 2);
    --location_font_size: calc(var(--vh, 1vh) * 2);
  }

  .location_address img {
    width: 20px;
    height: 20px;
  }

  .location_mail img {
    width: 20px;
    height: 20px;
  }

  .signature {
    font-size: 10px;
  }
}

@media only screen and (max-width: 1200px) and (min-width: 600px) {
  :root {
    --address_font_size: calc(var(--vh, 1vh) * 7);
    --address_sub_font_size: calc(var(--vh, 1vh) * 2);
    --location_font_size: calc(var(--vh, 1vh) * 2);
  }

  .location_address img {
    width: 40px;
    height: 40px;
  }

  .location_mail img {
    width: 40px;
    height: 40px;
  }

  .signature {
    font-size: 20px;
  }
}
