html,
body {
  margin: 0;
  padding: 0;
  height: 100%;
}

.section {
  height: 100vh;
}

body::-webkit-scrollbar {
  display: none;
}

@media only screen and (max-width: 600px) {
  .section {
    height: calc(var(--vh, 1vh) * 50);
  }
}

@media only screen and (max-width: 1200px) and (min-width: 600px) {
  .section {
    height: calc(var(--vh, 1vh) * 50);
  }
}
