:root {
  /* --development_font_size: 100px;
  --development_sub_font_size: 25px; */
  --development_font_size: 14vh;
  --development_sub_font_size: 5vh;
}

.development_background {
  background-color: #000000;
  height: 100%;
  display: flex;
}

.development_center {
  width: 80%;
  height: 80%;
  margin: auto;
}

.title {
  width: 100%;
  height: 40%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.title p {
  margin: 0;
  color: #ffffff;
  font-size: var(--development_font_size);
  font-weight: 700;
}

.contact {
  width: 100%;
  height: 10%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.contact p {
  margin: 0;
  color: #ffffff;
  font-size: var(--development_sub_font_size);
  font-weight: 300;
}

.app_store {
  width: 100%;
  height: 50%;

  display: flex;
  justify-content: center;
  align-items: center;
}

.app_sore_img_1 {
  margin-right: 70px;
}

@media only screen and (max-width: 600px) {
  :root {
    --development_font_size: calc(var(--vh, 1vh) * 5);
    --development_sub_font_size: calc(var(--vh, 1vh) * 2);
  }

  .title p {
    text-align: center;
  }

  .app_store img {
    width: 50px;
    height: 50px;
  }
}

@media only screen and (max-width: 1200px) and (min-width: 600px) {
  :root {
    --development_font_size: calc(var(--vh, 1vh) * 5);
    --development_sub_font_size: calc(var(--vh, 1vh) * 2);
  }

  .title p {
    text-align: center;
  }

  .app_store img {
    width: 80px;
    height: 80px;
  }
}
