:root {
  --introduction_font_size: 10vh;
}

.introduction_background {
  background-color: #ffffff;
  height: 100%;
  display: flex;
}

.introduction_center {
  width: 80%;
  height: 80%;
  margin: auto;
  display: flex;
  align-items: center;
}

.introduction_center p {
  width: 100%;
  margin: 0;
  font-size: var(--introduction_font_size);
  text-align: center;
}

@media only screen and (max-width: 600px) {
  :root {
    --introduction_font_size: calc(var(--vh, 1vh) * 4);
  }
}

@media only screen and (max-width: 1200px) and (min-width: 600px) {
  :root {
    --introduction_font_size: calc(var(--vh, 1vh) * 5);
  }
}
